import store from "../redux/store/index";
import { updateState } from "../redux/actions/index";
import localForage from "localforage";
import mergeWith from "lodash.mergewith";

import ObjectService from "../services/ObjectService";
import StringHelper from "../helpers/StringHelper";

const default_settings = {
	intrinsic : {
		table_type      : "dps",
		collapsed       : false,
		current_version : null,
		last_version    : null,
		player_blur     : false
	},
	interface : {
		player_name           : "YOU",
		opacity               : 100,
		zoom                  : 100,
		top_right_rank        : false,
		blur_job_icons        : false,
		collapse_down         : false,
		light_theme           : false,
		minimal_theme         : false,
		footer_when_collapsed : false,
		footer_dps            : false,
		decimal_accuracy      : 2,
		shorten_thousands     : false,
		language              : "en",
		auto_hide             : false,
		auto_hide_delay       : 0
	},
	custom : {
		css          : "",
		metric_names : {}
	},
	table_settings : {
		general : {
			table : {
				short_names      : "no_short",
				footer_at_top    : false,
				percent_bars     : true,
				prioritize_party : false
			},
			raid  : {
				short_names      : "no_short",
				percent_bars     : true,
				prioritize_party : false
			}
		},
		dps     : {
			show_footer : true
		},
		heal    : {
			show_footer : true
		},
		tank    : {
			show_footer : true
		}
	},
	table_columns : {
		dps : [
			"damage%",
			"encdps",
			"crithit%",
			"DirectHitPct",
			"CritDirectHitPct"
		],
		heal : [
			"healed%",
			"effective_heal_pct",
			"enchps",
			"effective_hps",
			"OverHealPct",
			"critheal%"
		],
		tank : [
			"damage_taken_pct",
			"enctps",
			"damagetaken",
			"healstaken",
			"BlockPct",
			"ParryPct",
			"deaths"
		],
		raid : {
			dps  : [
				"encdps",
				"enchps"
			],
			heal : [
				"enchps",
				"encdps"
			],
			tank : [
				"encdps",
				"enchps"
			]
		}
	},
	sort_columns : {
		dps  : "damage",
		heal : "healed",
		tank : "damagetaken",
		raid : "damage"
	},
	detail_data : {
		dps : [
			"damage%",
			"encdps",
			"damage",
			"crithit%",
			"DirectHitPct",
			"CritDirectHitPct",
			"max_hit_format"
		],
		heal : [
			"healed%",
			"effective_heal_pct",
			"enchps",
			"effective_hps",
			"healed",
			"OverHealPct",
			"critheal%",
			"max_heal_format"
		],
		tank : [
			"damage_taken_pct",
			"damagetaken",
			"enctps",
			"healstaken",
			"BlockPct",
			"deaths"
		]
	}
};

class Settings {
	loadSettings() {
		return new Promise((resolve, reject) => {
			console.log("gh1");
			localForage.getItem("settings_cache")
				.then((data) => {
					console.log("gh2");
					let result = this.mergeSettings(data);

					store.dispatch(
						updateState({
							key   : "settings",
							value : this.settings
						})
					);
					console.log("gh3");
					resolve(result);
				})
				.catch((e) => {
					reject();
				});
		});
	}

	saveSettings(force) {
		return new Promise((resolve, reject) => {
			console.log("gh11.1");
			if (!window.parser && !force) {
				console.log("gh11.2");
				resolve();
				return;
			}

			console.log("gh11.3");

			localForage.setItem("settings_cache", JSON.stringify(this.settings || default_settings))
				.then(() => {
					console.log("gh11.4");
					this.saveToOverlayPlugin();
					console.log("gh11.5");
					resolve();
				})
				.catch((e) => {
					console.log(e);
					console.log("gh11.6");
					reject();
				});
		});
	}

	mergeSettings(data) {
		let json                 = false;
		let tmp_default_settings = Object.assign({}, JSON.parse(JSON.stringify(default_settings)));
		let result               = {
			used_default : false
		};

		try {
			json = JSON.parse(data);
		} catch (e) {}

		if (!json) {
			json = tmp_default_settings;

			result.used_default = true;
		} else {
			json = mergeWith(tmp_default_settings, json, function(obj_val, src_val) {
				if (Array.isArray(obj_val)) {
					return (src_val) ? src_val : obj_val;
				}
			});
		}

		this.settings = json;

		return result;
	}

	importSettings(settings_key) {
		return new Promise((resolve, reject) => {
			console.log("gh34");
			let data = StringHelper.fromBinary(atob(settings_key));

			this.mergeSettings(data);
			this.saveSettings(true)
				.then(() => {
					store.dispatch(
						updateState({
							key   : "settings",
							value : this.settings
						})
					);
					resolve();
				});
		});
	}

	getSetting(key) {
		return (key.indexOf(".") !== -1) ? ObjectService.getByKeyPath(this.settings, key) : this.settings[key] || default_settings[key];
	}

	setSetting(key_path, value, skip_save) {
		ObjectService.setByKeyPath(this.settings, key_path, value);

		if (!skip_save) {
			this.saveSettings();
		}
	}

	getExportKey() {
		return btoa(StringHelper.toBinary(JSON.stringify(this.settings)));
	}

	getOverlayPluginKey() {
		return `ember-settings-${process.env.REACT_APP_ENV}`;
	}

	saveToOverlayPlugin() {
		console.log("gh11.7");
		let service = store.getState().plugin_service;
		console.log("gh11.8");

		if (!service.plugin_service.isNgld() || service.is_websocket) {
			console.log("gh11.9");
			return false;
		}

		console.log("gh11.10");

		let key     = this.getOverlayPluginKey();
		let message = service.plugin_service.createMessage("saveData", key, this.getExportKey());

		console.log("gh11.11");

		service.callHandler(message);
		console.log("gh11.12");
	}

	restoreFromOverlayPlugin() {
		return new Promise((resolve, reject) => {
			console.log("gh15");
			let service = store.getState().plugin_service;
			console.log("gh16");

			if (!service.plugin_service.isNgld()) {
				console.log("gh17.1");
				resolve();
				return;
			}

			console.log("gh17.2");

			let key      = this.getOverlayPluginKey();
			console.log("gh18");
			let message  = service.plugin_service.createMessage("loadData", key);
			console.log("gh19");
			let callback = (data) => {
				console.log("gh21");
				if (!data.data) {
					console.log("gh22.1");
					return;
				}

				console.log("gh22.2");

				data = JSON.parse(data.data || {})

				console.log("gh23");

				if (data["$isNull"]) {
					console.log("gh24");
					service.plugin_service.resetCallback();
					console.log("gh25");
					reject();
					return;
				}

				console.log("gh26");

				if (!data.key || data.key !== key) {
					console.log("gh27");
					return;
				}

				if (!data.data) {
					console.log("gh28");
					service.plugin_service.resetCallback();
					console.log("gh29");
					reject();
					return;
				}

				console.log("gh30");

				this
					.importSettings(data.data)
					.then(() => {
						console.log("gh31");
						service.plugin_service.resetCallback();
						resolve();
					})
					.catch(() => {
						console.log("gh32");
						service.plugin_service.resetCallback();
						reject();
					});
			};

			console.log("gh20");

			service
				.plugin_service
				.callHandler(message, callback);
		});
	}

	restoreFromOverlayPluginIfNecessary(necessary) {
		return new Promise((resolve, reject) => {
			console.log("gh13");
			if (!necessary) {
				console.log("gh14.1");
				resolve();
				return;
			}

			console.log("gh14.2");

			this
				.restoreFromOverlayPlugin()
				.then(() => {
					resolve();
				});
		});
	}
}

export default new Settings();