// React
import React from "react";
import ReactDOM from "react-dom";
import { HashRouter as Router, Route } from "react-router-dom";
import localForage from "localforage";

// Redux
import { Provider } from "react-redux";
import store from "./redux/store";

// Styles
import "semantic-ui-less/semantic.less";
import "./index.css";

// Other React
import * as serviceWorker from "./serviceWorker";

// Custom
import Parser from "./components/Parser";
import Settings from "./components/Settings";
import MigrationService from "./services/MigrationService";

// localForage.setItem("settings_cache", "{\"intrinsic\":{\"table_type\":\"raid\",\"collapsed\":false,\"current_version\":\"0.22.0-alpha\",\"last_version\":\"0.21.0-alpha\",\"player_blur\":false},\"interface\":{\"player_name\":\"YOU\",\"opacity\":100,\"zoom\":110,\"top_right_rank\":false,\"blur_job_icons\":false,\"collapse_down\":false,\"light_theme\":false,\"minimal_theme\":false,\"footer_when_collapsed\":false,\"footer_dps\":true,\"decimal_accuracy\":2,\"shorten_thousands\":true,\"language\":\"en\",\"auto_hide\":true,\"auto_hide_delay\":199},\"custom\":{\"css\":\"\",\"metric_names\":{}},\"table_settings\":{\"general\":{\"table\":{\"short_names\":\"no_short\",\"footer_at_top\":false,\"percent_bars\":false,\"prioritize_party\":false},\"raid\":{\"short_names\":\"no_short\",\"percent_bars\":false,\"prioritize_party\":false}},\"dps\":{\"show_footer\":true},\"heal\":{\"show_footer\":true},\"tank\":{\"show_footer\":true}},\"table_columns\":{\"dps\":[\"damage%\",\"encdps\",\"crithit%\",\"DirectHitPct\",\"CritDirectHitPct\",\"deaths\"],\"heal\":[\"healed%\",\"effective_heal_pct\",\"enchps\",\"effective_hps\",\"OverHealPct\",\"critheal%\"],\"tank\":[\"damage_taken_pct\",\"enctps\",\"damagetaken\",\"healstaken\",\"BlockPct\",\"deaths\"],\"raid\":{\"dps\":[\"encdps\",\"enchps\"],\"heal\":[\"enchps\",\"encdps\"],\"tank\":[\"encdps\",\"enchps\"]}},\"sort_columns\":{\"dps\":\"damage\",\"heal\":\"healed\",\"tank\":\"damagetaken\",\"raid\":\"damage\"},\"detail_data\":{\"dps\":[\"damage%\",\"encdps\",\"damage\",\"crithit%\",\"DirectHitPct\",\"CritDirectHitPct\",\"max_hit_format\"],\"heal\":[\"healed%\",\"effective_heal_pct\",\"enchps\",\"effective_hps\",\"healed\",\"OverHealPct\",\"critheal%\",\"max_heal_format\"],\"tank\":[\"damage_taken_pct\",\"damagetaken\",\"enctps\",\"healstaken\",\"BlockPct\",\"deaths\"]}}");

console.info(`Welcome and thank you for using Ember Overlay! Join the Discord at ${process.env.REACT_APP_DISCORD_URL}. GitHub is located at ${process.env.REACT_APP_GITHUB_URL}.`);

let state = store.getState();

state
	.settings_data
	.loadSettings()
	.then((settings_result) => {
		console.log("gh4");
		MigrationService
			.migrate()
			.then(() => {
				console.log("gh12");
				state
					.settings_data
					.restoreFromOverlayPluginIfNecessary(settings_result.used_default)
					.then(() => {
						ReactDOM.render(
							<Router basename={process.env.REACT_APP_ROUTER_BASE}>
								<Provider store={store}>
									<Route exact path="/" component={Parser}/>
									<Route path="/settings" component={Settings}/>
								</Provider>
							</Router>,
							document.getElementById("root")
						);
					});
			});
	})
	.catch((e) => {

	});

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();